import React from "react"
import { StaticQuery, graphql, Link } from "gatsby"
import List from "../assets/svg/list.svg"
import General from "../assets/svg/general.svg"
import Design from "../assets/svg/design.svg"
import Development from "../assets/svg/development.svg"
import Deploy from "../assets/svg/deploy.svg"

const SubHeader = () => (
  <StaticQuery
    query={subHeaderQuery}
    render={data => {
      const { subHeader } = data.site.siteMetadata

      return (
        <header className="sub-header">
          <nav>
            <ul>
              {subHeader.map(item => {
                const { link, cod, name } = item

                return (
                  <li className="group" key={cod}>
                    <Link
                      className={cod > 2 ? "group-link disable" : "group-link"}
                      to={link}
                    >
                      <span>{verifyName(name)}</span>
                      <span className="text">{name}</span>
                    </Link>
                  </li>
                )
              })}
            </ul>
          </nav>
        </header>
      )
    }}
  />
)

const verifyName = name => {
  switch (name) {
    case "All":
      return <List />
    case "General":
      return <General />
    case "Design":
      return <Design />
    case "Development":
      return <Development />
    case "Deployment":
      return <Deploy />
    default:
      return null
  }
}

const subHeaderQuery = graphql`
  query {
    site {
      siteMetadata {
        subHeader {
          cod
          link
          name
        }
      }
    }
  }
`

export default SubHeader
